img {
    max-width: 100%;
    max-height: 100%;
}

a#logo {
    width: 160px;
    height: 45px;
    background: url('/img/logo.png') left center/160px no-repeat;
}

.grecaptcha-badge {
    visibility: hidden;
}

.card-header > .wpp {
    background-color: #34af23;
}

.card-header > .tlg {
    background-color: #0088cc;
}

.card-header > .igs {
    background: linear-gradient(to top, #fbad50, #8a3ab9);
}

.card-header > .twt {
    background-color: #00acee;
}

.card-header > .pls {
    background-color: #000;
    color: #fff;
}

.card-header > button {
    padding: 5px;
    width: 30px;
    border-radius: 30px;
    height: 30px;
    line-height: 20px;
    font-size: 20px;
}

.card-header i,
.share a {
    color: #fff;
}

.card-header {
    width: 100%;
    text-align: center;
}

img.loja {
    max-width: 50px;
    margin-bottom: 10px;
    text-align: center;
}

.categoria > a > img {
    height: 100px;
    max-width: 200px;
}

#categorias > .categoria a {
    margin-top: 10px;
}

.categoria {
    height: 140px;
    width: 240px;
    border: 1px solid grey;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 15px;

}

.categoria > a {
    color: #000;
    text-decoration: none;
    text-align: center;
}

#social > a {
    margin-right: 10px;
}

.wp-button {
    width: 60px;
    height: 60px;
    background-color: #34af23;
    position: fixed;
    bottom: 5%;
    right: 5%;
    padding-top: 10px;
}

.wp-button > a {
    line-height: 35px;
    color: #fff;
    font-size: 35px;
}

.categoria img {
    max-width: 115px;
}

.pointer {
    cursor: pointer;
}

.product-image,
.loja {
    max-height: 300px;
}

#logomarca > img {
    width: 250px;
}

img[alt=Extra],
img[alt=Ponto] {
    background-color: #606060;
    padding: 10px;
}

.loja-image[alt=Extra],
.loja-image[alt=Ponto],
.loja-image[alt="Casas Bahia"] {
    margin-bottom: 3rem;
    margin-top: 2rem;
}

#close {
    position: absolute;
    top: 20px;
    right: 20px;
}

.z-index-fixed {
    z-index: 1050;
}

.padding-menu {
    padding-top: 61px;
}

@media (max-width: 768px) {
    #menu {
        font-size: calc(1.375rem + 1.5vw) !important;
    }

    .padding-menu {
        padding-top: 116px;
    }
}

@media (min-width: 768px) {
    .toast {
        width: 500px;
    }

    div.card-body {
        position: relative;
        top: 40%;
        transform: translateY(-50%);
        flex: none;
    }

    div.final {
        margin-top: auto;
    }
}

@media (min-width: 800px) {
    a#logo {
        width: 235px;
        height: 61px;
        background: url('/img/logo.png') left center/235px no-repeat;
    }
}

@media (min-width: 992px) {
    .col-lg-3-5 {
        width: 30%;
        flex: 0 0 auto;
    }

    div.toast-footer {
        margin-top: auto;
    }

    .toast {
        width: 800px;
    }
}
